import React, { Component } from 'react';
import CardNews from './CardNews';
import ReactJS from '../../assets/images/ReactJS.png';


const news=[
    {'title':"Gii thiu v ReactJS phn 1 (Cc khi nim c bn)",'content':'React l mt th vin UI pht trin ti Facebook  h tr vic xy dng nhng thnh phn (components).','image':ReactJS},
    {'title':"Gii thiu v ReactJS phn 1 (Cc khi nim c bn)",'content':'React l mt th vin UI pht trin ti Facebook  h tr vic xy dng nhng thnh phn (components.','image':ReactJS},
    {'title':"Gii thiu v ReactJS phn 1 (Cc khi nim c bn)",'content':'React l mt th vin UI pht trin ti Facebook  h tr vic xy dng nhng thnh phn (components).','image':ReactJS},
    {'title':"Gii thiu v ReactJS phn 1 (Cc khi nim c bn)",'content':'React l mt th vin UI pht trin ti Facebook  h tr vic xy dng nhng thnh phn (components).','image':ReactJS},
    {'title':"Gii thiu v ReactJS phn 1 (Cc khi nim c bn)",'content':'React l mt th vin UI pht trin ti Facebook  h tr vic xy dng nhng thnh phn (components).','image':ReactJS},
    {'title':"Gii thiu v ReactJS phn 1 (Cc khi nim c bn)",'content':'React l mt th vin UI pht trin ti Facebook  h tr vic xy dng nhng thnh phn (components).','image':ReactJS},

]

class News extends Component {

    constructor(props) {
        super(props);
        this.state={
            news
        }
    }

    renderNews(){
      var news=  this.state.news.map((news,key)=>{
            return (
              <div className="col-6" key={key}>
                <CardNews news={news}  />
              </div>
            )
        })

        return news;
    }
    render() {
        return (
            <div className="container mt-5">
                <div className="text-center">
                    <h3>Tin tc</h3>

                </div>
                <div className="row mt-3">
                    {this.renderNews()}
                </div>
            </div>

        );
    }
}

export default News;



