import React, { Component } from 'react';
import pp_hoc from '../../assets/images/pp_hoc.png';
import teacher from '../../assets/images/giang_vien.png';
import support from '../../assets/images/support.png';
import equipment from '../../assets/images/equipment.png';
import CardInfo from './CardInfor'
const cards=[
    {"src":pp_hoc, "title":"Phng php hc tin tin","content":"M hnh hc tp  gip gii quyt c nhiu vn  thc t v v cng tin li "},
    {"src":teacher,"title":"Gio vin ging dy tn tnh","content":"i ng gio vin vi trnh  chuyn mn cao s m lp bt c lc no bn cn"},
    {"src":equipment,"title":"Cung cp dch v tuyt vi","content":"Chng trnh qun l hc tp cho tng thnh vin"},
    {"src":support,"title":"H tr hc tp phong ph","content":"Phng php hc tp ph hp vi mi i tng"}]


class Introduce extends Component {

    constructor(props){
        super(props)
        this.state={
            srcImages:cards
        }
    }
    renderInfo(){
        var cards=this.state.srcImages.map((item,key)=>{
            return (
                <div key={key} className="col-12 col-sm-12 col-md-6 col-lg-3 col-xl-3 ">
                    <CardInfo info={item}  ></CardInfo>
                </div>
            )
        })
        return cards;
    }

    render() {
        return (
            <div>
                <div className="text-center mt-5 container">
                    <h2>Welcome to Classroom Online</h2>

                    <div className="row mt-5">
                        {this.renderInfo()}
                    </div>
                </div>
            </div>

        );
    }
}

export default Introduce;



